import { CODES_FOR_LANGS_WITHOUT_SPACES } from "../constants/languages";
export function checkLanguageCodeWithoutSpaces(sourceLanguage) {
    if (!sourceLanguage) {
        return false;
    }
    const isLanguageCodeWithoutSpaces = CODES_FOR_LANGS_WITHOUT_SPACES.some((value) => (sourceLanguage === value || sourceLanguage.split("-")[0] === value));
    return isLanguageCodeWithoutSpaces;
}
