import { useCallback, useState } from "react";
import { useQueryClient, useMutation } from "@tanstack/react-query";
import { textToSpeechRequest } from '../api';
import { useNotifier } from "../context/Notifier";
import { usePayment } from "../context/Payment";
import getLimitResetDate from "../utils/getLimitResetDate";
const requestLimitMessage = "Request limit exceeded. Please try again later";
const getAudioKey = (text, locale) => (["audio", text, locale]);
const fetchAudio = (setResetDate) => async ({ text, locale }) => {
    const response = await textToSpeechRequest({ text, locale });
    if (response.status === 429) {
        setResetDate(getLimitResetDate(response.headers.get("ratelimit-reset")));
        throw new Error(requestLimitMessage);
    }
    else {
        setResetDate(null);
    }
    if (!response.ok) {
        throw new Error("Something went wrong.");
    }
    const blob = await response.blob();
    return URL.createObjectURL(blob);
};
export const useAudioPlayer = () => {
    const [isPlaying, setIsPlaying] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const notify = useNotifier();
    const { setResetDate } = usePayment();
    const queryClient = useQueryClient();
    const { mutateAsync: loadAudio } = useMutation({
        mutationFn: fetchAudio(setResetDate),
        onSuccess: (data, variables) => {
            queryClient.setQueryData(getAudioKey(variables.text, variables.locale), data);
        },
        onError(error) {
            if (error.message.includes(requestLimitMessage)) {
                setIsLoading(false);
                return;
            }
            setIsLoading(false);
            notify({ message: "Something went wrong, please try again later" });
        }
    });
    const playAudio = useCallback(async (text, locale) => {
        setIsLoading(true);
        const cacheKey = getAudioKey(text, locale);
        let audioUrl = queryClient.getQueryData(cacheKey);
        if (!audioUrl) {
            audioUrl = await loadAudio({ text, locale });
        }
        const audio = new Audio(audioUrl);
        setIsLoading(false);
        setIsPlaying(true);
        audio.addEventListener("ended", () => setIsPlaying(false));
        audio.play();
    }, [loadAudio, queryClient]);
    return {
        playAudio,
        isPlaying,
        isLoading
    };
};
