import React, { createContext, useContext, useState, useCallback } from "react";
const initialData = {
    resetDate: null,
    isLoading: false,
    isOpen: false,
    onOpen: () => { },
    onClose: () => { },
    setIsLoading: () => { },
    setResetDate: () => { }
};
const PaymentContext = createContext(initialData);
const PaymentContextProvider = ({ children }) => {
    const [resetDate, setResetDate] = useState(initialData.resetDate);
    const [isLoading, setIsLoading] = useState(initialData.isLoading);
    const [isOpen, setIsOpen] = useState(initialData.isOpen);
    const onOpen = useCallback(() => {
        setIsOpen(true);
    }, []);
    const onClose = useCallback(() => {
        setIsOpen(false);
    }, []);
    const value = {
        resetDate,
        isLoading,
        isOpen,
        onOpen,
        onClose,
        setIsLoading,
        setResetDate
    };
    return (React.createElement(PaymentContext.Provider, { value: value }, children));
};
const usePayment = () => useContext(PaymentContext);
export { PaymentContextProvider, usePayment };
