import { AvailableLanguages } from "../types";
function detectLanguage() {
    const currentLanguage = navigator.language.split("-")[0].toLowerCase();
    // const currentLanguage = "es-ES".split("-")[0].toLowerCase();
    // const currentLanguage = "ru-RU".split("-")[0].toLowerCase();
    // const currentLanguage = "it-IT".split("-")[0].toLowerCase();
    // const currentLanguage = "ja".split("-")[0].toLowerCase();
    const locales = Object.values(AvailableLanguages);
    return locales.find((locale) => locale === currentLanguage);
}
function getInitLanguage() {
    return detectLanguage() || AvailableLanguages.English;
}
export default getInitLanguage;
