import React, { createContext, useContext, useState, useEffect } from "react";
import { getUserDataRequest } from "../../api";
const initialData = {
    isLoading: true,
    userData: null,
    billingInfo: null,
    priceInfo: { currency: "$", price: 8 },
    setUserData: () => { }
};
const AuthContext = createContext(initialData);
const AuthContextProvider = ({ children }) => {
    const [isLoading, setIsLoading] = useState(initialData.isLoading);
    const [userData, setUserData] = useState(initialData.userData);
    const [billingInfo, setBillingInfo] = useState(initialData.billingInfo);
    const [priceInfo, setPriceInfo] = useState(initialData.priceInfo);
    useEffect(() => {
        async function loadData() {
            try {
                const response = await getUserDataRequest();
                const data = await response.json();
                if (data && data.user) {
                    setUserData(data.user);
                }
                if (data && data.billingInfo) {
                    setBillingInfo(data.billingInfo);
                }
                if (data && data.priceInfo) {
                    setPriceInfo(data.priceInfo);
                }
                setIsLoading(false);
            }
            catch (error) {
                console.error(error);
            }
        }
        loadData();
    }, []);
    const value = {
        isLoading,
        userData,
        billingInfo,
        priceInfo,
        setUserData
    };
    return (React.createElement(AuthContext.Provider, { value: value }, children));
};
const useAuth = () => useContext(AuthContext);
export { AuthContextProvider, useAuth };
