import React, { createContext, useContext, useState, useCallback } from "react";
import { generateTextRequest } from "../../api";
import { useStorage } from "../Storage";
import { usePayment } from "../Payment";
import { useNotifier } from "../Notifier";
import getLimitResetDate from "../../utils/getLimitResetDate";
const initialData = {
    text: "",
    translations: [],
    exersizes: {},
    isLoading: false,
    onStart: () => { },
    onClear: () => { }
};
const ContentContext = createContext(initialData);
const ContentContextProvider = ({ children }) => {
    const storage = useStorage();
    const notify = useNotifier();
    const { setResetDate } = usePayment();
    const savedData = storage.onGetResults();
    const [text, setText] = useState(savedData.text || initialData.text);
    const [translations, setTranslations] = useState(savedData.translations || initialData.translations);
    const [exersizes, setExersizes] = useState(savedData.exersizes || initialData.exersizes);
    const [isLoading, setIsLoading] = useState(initialData.isLoading);
    const onClear = useCallback(() => {
        setText("");
        setTranslations([]);
        setExersizes({});
        storage.onRemoveResults();
    }, []);
    const onStart = useCallback(async ({ sourceLanguage, targetLanguage, topic, level }) => {
        setIsLoading(true);
        try {
            const response = await generateTextRequest({
                sourceLanguage,
                targetLanguage,
                topic,
                level
            });
            if (response.status === 429) {
                setIsLoading(false);
                setResetDate(getLimitResetDate(response.headers.get("ratelimit-reset")));
                return;
            }
            else {
                setResetDate(null);
            }
            if (!response.ok) {
                notify({ message: "Something went wrong, please try again later" });
                setIsLoading(false);
                return;
            }
            const data = await response.json();
            if (data && data.text && data.translations && data.exersizes) {
                onClear();
                setText(data.text);
                setTranslations(data.translations);
                setExersizes(data.exersizes);
                storage.onSetResults({ ...data, sourceLanguage, targetLanguage, topic, level });
            }
        }
        catch (error) {
            console.log(error);
            notify({ message: "Something went wrong, please try again later" });
        }
        setIsLoading(false);
    }, []);
    const value = {
        text,
        translations,
        exersizes,
        isLoading,
        onStart,
        onClear
    };
    return (React.createElement(ContentContext.Provider, { value: value }, children));
};
const useContent = () => useContext(ContentContext);
export { ContentContextProvider, useContent };
