const SERVER_URL = process.env.NODE_ENV === "production"
    ? process.env.SERVER_URL_PROD
    : process.env.SERVER_URL_DEV;
export function generateTextRequest(params) {
    return fetch(`${SERVER_URL}/api/generate-text`, {
        method: "POST",
        headers: {
            "Content-Type": "application/json"
        },
        body: JSON.stringify(params)
    });
}
export function textToSpeechRequest(params) {
    return fetch(`${SERVER_URL}/api/text-to-speech`, {
        method: "POST",
        headers: {
            "Content-Type": "application/json"
        },
        body: JSON.stringify(params)
    });
}
export function wakeUpServerRequest() {
    return fetch(`${SERVER_URL}/api/status`);
}
export function getUserDataRequest() {
    return fetch(`${SERVER_URL}/api/me`, {
        method: "GET",
        credentials: "include",
        headers: {
            "Content-Type": "application/json"
        }
    });
}
export function googleLogoutRequest() {
    return fetch(`${SERVER_URL}/auth/google/logout`, {
        credentials: "include"
    });
}
export function createCheckoutSessionRequest(userId) {
    return fetch(`${SERVER_URL}/api/stripe/create-checkout-session`, {
        method: "POST",
        headers: {
            "Content-Type": "application/json"
        },
        body: JSON.stringify({
            userId
        })
    });
}
