import React from "react";
import cn from "classnames";
import { useCopyText } from "../../hooks/useCopyText";
import styles from "./styles.css";
function CopyTextButton({ text, className, children }) {
    const onCopyText = useCopyText();
    if (!text) {
        return null;
    }
    return (React.createElement("button", { className: cn(styles.copyTextButton, className), onClick: onCopyText(text) }, children));
}
export default CopyTextButton;
